(function () {
    'use strict';

    Helper.$inject = ["$q", "$http", "settings"];
    angular.module('courses.services')
        .service('httpHelper', Helper);

    function Helper($q, $http, settings) {
        var self = this;

        self.getUrl = getUrl;
        self.get = function (url, params) {
            return _http(url, 'PUT', params);
        };
        self.post = function (url, data) {
            return _http(url, 'POST', undefined, data);
        };
        self.put = function (url, params, data) {
            return _http(url, 'PUT', params, data);
        };
        self.delete = function (url, params) {
            return _http(url, 'DELETE', params);
        };
        self.resource = resource;


        /// =================== ///

        function resource(url) {
            return {
                list: function () {
                    return self.get(url, {});
                },
                get: function (id) {
                    return self.get(url, { id: id });
                },
                add: function (object) {
                    return self.post(url, object);
                },
                update: function (id, object) {
                    return self.put(url, { id: id }, object);
                },
                remove: function (id) {
                    return self.delete(url, { id: id });
                }
            };
        }

        function _http(url, method, params, data) {
            return $http({
                method: method,
                url: getUrl(url),
                params: params,
                headers: { 'Content-Type': 'application/json ; ' },
                data: data
            }).then(function (response) {
                return response;
            }).catch(_fail);
        }

        function getUrl(url) {
            return settings.webserviceUrl + (url.startsWith('/') ? '' : '/') + url;
        }

        function _fail(response) {
            $q.reject(response);
        }
    }
})();
